@use '../../scss/index' as *;

.order{
    font-size: $standard;
    line-height: .7;
    &__box{
        display: flex;
        align-items: center;
        height: auto;
    }
    &__additives{
        display: flex;
        align-items: center;
        padding-left: 2rem;

        &__element{
            padding-left: .2rem;
        }
    }
    &__size{
        padding-left: 1rem;
        margin: 0;
    }
    &__index{
        margin: 0;
    }
    &__container{
        margin-top: 2rem;
        width: 80%;
        margin: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__title{
        margin: 2rem 4rem;
        font-size: $title;
        font-weight: 100;

    }
    &__cost{
        padding-left: 2rem;
    }
}

button{
    width: 150px;
    height: max-content;
    padding: 5px;
    margin-right: 10%;
    border-radius: .15em;
    border: .05em solid currentColor;
    cursor: pointer;
    font-family: $font-family;
    font-size: 1.5rem;
}

button:hover{
    background-color: #A88300;
}