@use '../../scss/index' as *;

.selects{
    width: 80%;
    margin: auto;
    &__header{
        display: flex;
        align-items: center;
        justify-content: center;
        &__title{
            font-size: $title;
            font-weight: 100;
        }
        &__elements{
            display: flex;
            align-items: center;

            .size{
                display: flex;
                align-items: center;
                padding: 2rem 2rem;
                &__small, &__medium, &__big{
                    padding: 5px;
                    margin-inline: .5rem;
                    border-radius: 50%;
                }
                &__small{
                    width: 6rem;
                    height: 6rem;
                }
                &__medium{
                    width: 7rem;
                    height: 7rem;
                }
                &__big{
                    width: 8rem;
                    height: 8rem;
                }
                .select{
                    background-color: #AF3B3B;
                }
            }
       
        }
    }
    &__additives__box{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;

        .additives{
            display: flex;
            justify-content: left;
            align-items: center;
            padding: 0 4rem;
           
            &__img{
                width: 50px;
                height: 50px;
                object-fit: contain;
                margin-inline: 1rem .5rem;
                
            }
            &__name{
                font-size: $standard;
            }
            &__price{
                font-size: 2.2rem;
            }
        }
        
        .additives div:last-child{
            margin-left: auto;
        }
    }
    &__price{
        &__container{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            margin: 0;
            margin-right: 10rem;
            
            button{
                width: 20%;
                padding: 5px;
                font-family: $font-family;
                font-size: 2rem;
                border-radius: .15em;
                border: .05em solid currentColor;
                cursor: pointer;
            }
            button:hover{
                background-color: #A88300;
            }
        }

        &__title{
            font-size: 3rem;
            font-weight: 100;
            margin: 1rem;
            padding-right: 5rem;
        }
    }
}



input[type="checkbox"] {
    /* Remove most all native input styles */
    appearance: none;
    /* For iOS < 15 */
    margin: 0;
    cursor: pointer;
    color: currentColor;
    width: 1.15em;
    height: 1.15em;
    border: 0.15em solid currentColor;
    border-radius: 0.15em;
    transform: translateY(-0.075em);
    display: grid;
    place-content: center;
  }
  
  input[type="checkbox"]::before {
    content: "";
    width: 0.65em;
    height: 0.65em;
    clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
    transform: scale(0);
    transform-origin: bottom left;
    transition: 120ms transform ease-in-out;
    background-color: #604A31;
  }
  
  input[type="checkbox"]:checked::before {
    transform: scale(1);
  }
  
  input[type="checkbox"]:focus {
    outline: max(2px, 0.15em) solid currentColor;
    outline-offset: max(2px, 0.15em);
   
  }

